.root {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0,255,0,.2);
  width: 100vw;
  min-height: 5em;
  text-align: center;
}
.top-sub {
  background-color: rgba(0,0,0,.2);
}
.top-sub.left {
  color: pink;
}
.top-sub.center {
  color: pink;
}
.top-sub.right {
  color: pink;
}
.sector {
  display: inherit;
  justify-content: inherit;
  align-items: inherit;
}
.sector.one {
  width: 100vw;
  height: calc(100vh - 5em);
  background-color: rgba(255,0,0,.2);
}
.sector.two {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,255,.2);
}
.sector.three {
  width: 100vw;
  height: 100vh;
  background-color: rgba(255,255,0,.2);
}
.sector.four {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,255,255,.2);
}
.sector.five {
  width: 100vw;
  height: 100vh;
  background-color: rgba(255,0,255,.2);
}